import React from "react";
import {
  StyleSheet,
  View,
  Pressable,
  Text,
  Image,
  Platform,
  Alert,
} from "react-native";
import { useNavigation } from "@react-navigation/native";
import { useState } from "react";
import { FIREBASE_AUTH } from "../../../firebaseconfig";
import Toast from "react-native-toast-message";
const isWeb = Platform.OS === "web";

export default function RestaurantButton({ label, image }) {
  const [isPressed, setIsPressed] = useState(false);
  const handlePressIn = () => {
    setIsPressed(true);
  };

  const handlePressOut = () => {
    setIsPressed(false);
  };
  const navigation = useNavigation();
  const user = FIREBASE_AUTH.currentUser;
  const buttonStyle = [
    styles.button,
    isPressed && styles.buttonPressed,
    isWeb && styles.buttonWeb, // Estilos específicos para a web
  ];

  let imageURL;
  if (typeof image === "number") {
    imageURL = Image.resolveAssetSource(image).uri;
  } else {
    imageURL = image;
  }

  const PreventPressWithouAccount = () => {
    if (user) {
      navigation.navigate(label);
    } else {
      Toast.show({
        type: "error",
        text1: "⚠️Atenção!",
        text2: "Realize o Login para Continuar!",
      });
    }
  };
  return (
    <View style={[styles.buttonContainer, isWeb && styles.containerWeb]}>
      <Pressable
        style={[styles.button, isPressed && styles.buttonPressed]}
        onPressIn={handlePressIn}
        onPressOut={handlePressOut}
        onPress={() => {
          PreventPressWithouAccount();
        }}
      >
        <View style={[styles.imageContainer]}>
          <Image source={{ uri: imageURL }} style={styles.buttonImage} />
        </View>
      </Pressable>
      <Text style={[styles.buttonLabel, { color: "#fff" }]}>{label}</Text>
    </View>
  );
}

const styles = StyleSheet.create({
  buttonContainer: {
    height: 480,
    width: 210,
    marginBottom: -220,
    alignItems: "center",
  },
  imageContainer: {},
  containerWeb: {
    // Largura máxima na web
    width: 230,
  },
  button: {
    width: 180,
    height: 180,
    borderRadius: 15,
    overflow: "hidden",
  },

  buttonLabel: {
    color: "#fff",
    fontSize: 25,
  },

  buttonImage: {
    width: 180,
    height: 195,
    borderRadius: 15,
  },
  buttonPressed: {
    transform: [{ scale: 1.05 }], // Aplica o zoom no Pressable pressionado
  },
  buttonWeb: {
    // Estilos específicos para a web
    // Por exemplo, adicionando uma sombra no botão apenas para web
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
  },
});
