import React, { useState, useEffect, useCallback } from "react";
import { View, Text, StyleSheet, Image } from "react-native";
import { StatusBar } from "expo-status-bar";
import Button from "../../Components/ButtonsHomePage";
import { FIREBASE_AUTH } from "../../../firebaseconfig";
import { useFocusEffect, useNavigation } from "@react-navigation/native";
import { HomeScreenStyles } from "./styles";

const PlaceholderImage = require("../../Assets/mercadotecaLogo.png");

export function HomeScreen({ navigation, route }) {
  const user = FIREBASE_AUTH.currentUser;
  const [refreshing, setRefreshing] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(!!user); // Check user's login status initially
    
  const refreshPage = useCallback(() => {
    setRefreshing(true);
    setTimeout(() => {
      setRefreshing(false);
    }, 100); // Adjust the delay as needed

    // Update the isLoggedIn state based on user's login status
    setIsLoggedIn(!!user);
  }, [user]); // Include user in the dependency array

  useEffect(() => {
    // Set the isLoggedIn state initially when the component mounts
    setIsLoggedIn(!!user);
  }, [user]);

  useFocusEffect(refreshPage); // Run the effect when the screen comes into focus

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />
      <View style={styles.imageContainer}>
        <Image source={PlaceholderImage} style={styles.image} />
      </View>
      <View style={styles.buttonContainer}>
        <Text
          style={{
            color: "#fff",
            alignSelf: "center",
            paddingBottom: 50,
            fontSize: 23,
            fontWeight: 700,
          }}
        >
          BEM VINDO À MERCADOTECA, {user ? user.displayName : ":)"}!
        </Text>

        {refreshing ? (
          // Render something while refreshing, e.g., a loading spinner
          <></>
        ) : !isLoggedIn ? (
          <>
            <Button label="Menu" theme="menu" onPress={refreshPage} />
            <Button label="Login" theme="redirectlogin" onPress={refreshPage} />
            <Button label="Cadastrar" onPress={refreshPage} />
          </>
        ) : (
          <>
            <Button label="Menu" theme="menu" onPress={refreshPage} />
            <Button label="Perfil" theme="redirectprofile" onPress={refreshPage} />
          </>
        )}
      </View>
    </View>
  );
}

const styles = HomeScreenStyles;
