import * as React from "react";
import {
  View,
  Text,
  StyleSheet,
  SafeAreaView,
  FlatList,
  StatusBar,
  SectionList,
  Pressable,
} from "react-native";
import MenuItemButton from "../../../Components/MenuItems";
import { data } from "../../../../BackEnd/DataBase/AllRestaurants/AllRestaurantData";
import { menu } from "../../../../BackEnd/DataBase/Trovatta/MenuTrovatta";
import { ScrollView } from "react-native-gesture-handler";


export function TrovattaMainScreen() {
  const [activeCategories, setActiveCategories] = React.useState([]);
  const [visibleRestaurantIndex, setVisibleRestaurantIndex] = React.useState(0);

  const renderMenuItem = ({ item }) => (
    <MenuItemButton
      text={item.data}
      label={item.title}
      image={item.logo}
      price={item.price}
    />
  );

  const renderSectionHeader = ({ section: { categories } }) => (
    <View style={styles.categoriesContainer}>
      {categories.map((category) => (
        <Pressable
          key={category}
          style={[
            styles.categories,
            activeCategories.includes(category) && styles.categoriesActive,
          ]}
          onPress={() => toggleCategory(category)}
        >
          <Text style={styles.categoriesText}>{category}</Text>
        </Pressable>
      ))}
    </View>
  );

  const toggleCategory = (category) => {
    setActiveCategories((prevActiveCategories) => {
      if (prevActiveCategories.includes(category)) {
        return [];
      } else {
        return [category];
      }
    });
  };

  return (
    <SafeAreaView style={styles.container}>
      

<ScrollView style={styles.menuItemsContainer}>
        {menu.map((item, index) => (
          <MenuItemButton
            key={index}
            text={item.data}
            label={item.title}
            image={item.logo}
            price={item.price}
          />
        ))}
      </ScrollView>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: "#231F20",
    flex: 1,
    paddingTop: StatusBar.currentHeight,
  },
  imageContainer: {
    width: 200,
    height: 150,
    borderRadius: 10,
    overflow: "hidden",
  },
  image: {
    flex: 1,
  },
  item: {
    flexDirection: "row",
    alignItems: "center",
    backgroundColor: "#F2B33D",
    padding: 10,
    marginVertical: 8,
    marginHorizontal: 16,
    borderRadius: 9,
  },
  descriptionContainer: {
    flex: 1,
    paddingLeft: 10,
  },
  header: {
    height: 3, // Adjust the height as needed
  },
  list: {
    marginVertical: 10,
  },
  categoriesContainer: {
    width: "100%",
    maxHeight: 200 ,
    flexDirection: "row",
    paddingBottom: 10,
    overflow: "hidden",
    justifyContent: "center",
    alignItems: "center"
  },
  categories: {
    backgroundColor: "#fff",
    height: 20,
    width: 130,
    marginHorizontal: 3,
    marginVertical: 5,
    padding: 2,
    borderRadius: 10,
    justifyContent: "center",
  },
  categoriesActive: {
    backgroundColor: "#F2B33D", // Cor quando a categoria estiver ativa
  },
  categoriesText: {
    color: "#000",
    textAlign: "center",
    fontSize: 12,
  },
});
